@import 'vars';
html {
    width: 100%;
    &.fixed-body {
        overflow-y: hidden;
    }
}

.wrapper {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
}

.content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    width: 100%;
}

.m-page {
    width: calc(100vw - (100vw - 100%));
    overflow-x: hidden;
    font-family: $font1;
    font-weight: 500;
    color: $black;
    background-color: $gray3;
    font-size: calc(1440px * (16 / 1440));
    @media screen and (max-width: 1440px){
        font-size: calc(100vw * (16 / 1440));
    }
    &.fixed-body,
    &.fixed-body2{
        overflow-y: hidden;
    }
}

.container {
    max-width: 1440px;
    width: 100%;
    margin: 0 auto;
    padding: 0 10em;
    &._big{
        @media screen and (min-width: 993px){
            padding: 0 4.375em;
        }
    }
}

a {
    &:hover {
        color: $primary;
    }
}


@media screen and (max-width: 992px){
    .m-page{
        font-size: calc(100vw * .0208333);
    }

    .container{
        padding: 0 .625em;
    }
}


@media screen and (max-width: 576px){
    .m-page{
        font-size: calc(100vw * (16 / 420));
    }
}
