.header{
  padding: 1em 0;

  &__inner{
    min-height: 3em;
    display: flex;
    align-items: center;
    position: relative;
  }
  &__logo{
    display: block;
    width: 7.0625em;
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    img{
      width: 100%;
      display: block;
    }
  }
  &__phone{
    display: flex;
    align-items: center;
    white-space: nowrap;
    img{
      width: 1.5em;
      flex-shrink: 0;
      margin-right: .75em;
    }
  }
}

.burger{
  display: none;
}

.menu{
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  &:before{
    content: '';
    display: block;
    width: 7.0625em;
    flex-shrink: 0;
  }
}

.nav{
  display: flex;
  align-items: center;
  &__link{
    display: flex;
    align-items: center;
    height: 2.25em;
    padding: 0 .625em;
    font-weight: 600;
    span{
      font-size: .875em;
      line-height: 143%;
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    &__inner{
      min-height: unset;
      justify-content: space-between;
    }
    &__logo{
      position: relative;
      top: unset;
      transform: unset;
      z-index: 11;
    }
    &__phone{
      margin-top: 2em;
    }
    &._open{
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
      .menu{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }

  .burger{
    z-index: 11;
    width: 1.75em;
    height: 1.75em;
    position: relative;
    display: block;
    img{
      @include centerAbsolute;
      width: 100%;
      display: block;
      opacity: 0;
      transition: all .3s;
    }
    ._view1{
      opacity: 1;
    }
  }

  .menu{
    position: fixed;
    top: 0;
    left: 0;
    height: $fullVH;
    width: 100%;
    background-color: $gray2;
    z-index: 10;
    padding: 4.5em .75em 2em;
    overflow-y: auto;
    flex-direction: column;
    align-items: flex-start;
    opacity: 0;
    pointer-events: none;
    transition: all .4s;
    &:before{
      display: none;
    }
  }

  .nav{
    flex-direction: column;
    align-items: flex-start;
    &__link{
      margin: 0 0 1em 0;
      padding: 0;
      height: unset;
    }
  }
}
