.fg {
  position: relative;
  margin: 0;
  input {
    border: 1px solid #fff;
    background-color: #fff;
    padding: 0 1.14em;
    height: 3.5em;
    width: 100%;
    border-radius: .57em;
    font-size: .875em;
    color: $black;
    &::placeholder {
      color: $gray;
    }
    &:focus{
      border-color: $gray;
    }
  }
  &>label{
    margin-bottom: 1em;
    display: block;
  }
  ._success-field{
    background: #EAFFF2;
    border-color: #EAFFF2;
  }
  ._error-field{
    background: #FFEAEA;
    border-color: #FFEAEA;
  }
}


.m-check,
.m-switch{
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check {
  label {
    padding: .22em 0 .22em 2em;
    position: relative;
    min-height: 1.5em;
    cursor: pointer;
    display: block;
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      background-color: #fff;
      width: 1.5em;
      height: 1.5em;
      left: 0;
      top: 0;
      transition: all .100s;
    }
  }
  .text14{
    line-height: 121%;
    display: block;
  }
}

.m-switch{
  display: flex;
  align-items: center;
  input:checked+label{
    &:before{
      background-color: $green;
      left: 1.375em;
    }
  }
  label{
    width: 3.1875em;
    height: 1.9375em;
    flex-shrink: 0;
    margin-right: .5em;
    background-color: $gray2;
    border-radius: 2em;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      top: .125em;
      left: .125em;
      width: 1.6875em;
      height: 1.6875em;
      background-color: #fff;
      filter: drop-shadow(0px 3px 1px rgba(0, 0, 0, 0.06)) drop-shadow(0px 3px 8px rgba(0, 0, 0, 0.15));
      border-radius: 50%;
      transition: left .4s, background-color .2s;
    }
  }
}

.m-check{
  input{
    &:checked+label:before{
      background-image: url(../img/check.svg);
    }
  }
  label{
    &:before{
      border-radius: .375em;
      @include bgImgSize(1em);
    }
  }
}
