.main{
  position: relative;
  padding: .87em 0 2.5em;
  &:before{
    content: '';
    position: absolute;
    height: 100%;
    max-height: 80%;
    width: 100%;
    background-image: url(../img/main-bg.svg);
    background-size: 100% auto;
    background-repeat: no-repeat;
    top: 0;
    left: 0;
    pointer-events: none;
  }
  &__top{
    position: absolute;
    top: 3.94em;
    left: 50%;
    transform: translateX(-50%);
    z-index: 3;
    display: flex;
    flex-direction: column;
    align-items: center;
    .calc-btn__wrap{
      margin-top: 2em;
    }
  }
}

.home{
  position: relative;
  z-index: 2;
  &__img{
    img{
      width: 100%;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
}

.home-item{
  position: absolute;
  &._active{
    .home-card{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__toggle{
    padding: .75em;
    background-color: rgba(#fff,.95);
    border-radius: .75em;
  }
  .home-card{
    position: absolute;
    bottom: 100%;
    margin-bottom: .9375em;
  }
  &._bottom{
    .home-card{
      bottom: unset;
      top: 100%;
      margin: .9375em 0 0;
    }
  }
  &._right{
    .home-card{
      bottom: 50%;
      left: unset;
      right: 100%;
      margin: 0 .9375em 0 0;
      transform: translate(0%,30%);
    }
  }
}

.home-card{
  left: 50%;
  transform: translateX(-50%);
  width: 19.375em;
  transition: all .4s;
  opacity: 0;
  pointer-events: none;
  z-index: 4;
  &__img{
    img{
      width: 100%;
      display: block;
      border-radius: 1.875em 1.875em 0 0;
    }
  }
  &__content{
    padding: 1.5em;
    border-radius: 0 0 1.875em 1.875em;
    background: #FFF;
    box-shadow: 0 2.125em 4.625em 0 rgba(0, 0, 0, 0.10);
    backdrop-filter: blur(17px);
    @include grid100gap(.75em);
  }
}

.home-slider,
.mob-rect{
  display: none;
}

.can{
  padding: 2.5em 0;
  .info-block{
    max-width: 22em;
    gap: 1.25em;
  }
  &__top{
    margin-bottom: 1.25em;
    display: flex;
    justify-content: center;
  }
  &__items{
    display: flex;
    flex-wrap: wrap;
    width: calc(100% + 1.5em);
    margin-top: 2.5em;
  }
}

.solution{
  padding: 2.5em 0 3.75em;
  .info-block{
    gap: 1.25em;
  }
  &__grid{
    margin: 4em auto 0;
    width: 54.75em;
    max-width: 100%;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 1.25em));
    gap: 1.875em;
  }
}

.map{
  margin-top: 3.75em;
  height: 23.75em;
}

.opportunity{
  padding: 3.125em 0 3.75em;
  &__top{
    margin: 0 auto;
    max-width: 35em;
    text-align: center;
  }
  &__grid{
    margin-top: 4em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.25em));
    gap: 2.5em;
  }
}

.guarantee{
  padding: 3.75em 0;
  &__inner{
    padding: 2.12em 4.38em 3.3125em;
    border-radius: 1.875em;
    background-image: url(../img/guarantee-bg.webp);
    background-size: cover;
    background-position: center;
    position: relative;
    overflow: hidden;
  }
  &__content{
    @include grid100gap(1.375em);
    max-width: 31em;
  }
  &__top,
  &__nav{
    display: flex;
  }
  .badge{
    background: rgba(0, 0, 0, 0.07);
    color: $black;
  }
  &__img{
    position: absolute;
    right: -6em;
    top: -.5em;
    width: 36.875em;
  }
}

.consult{
  padding: 3.75em 0;
  &__inner{
    padding: 2em 2.25em 1.3125em;
    border-radius: 1.875em;
    background: linear-gradient(309deg, rgba(0, 240, 255, 0.00) 0%, rgba(0, 178, 255, 0.30) 100%), linear-gradient(127deg, rgba(255, 153, 0, 0.00) 0%, rgba(255, 153, 0, 0.30) 100%), #E8E8E8;
    backdrop-filter: blur(11px);
    position: relative;
  }
  &__img{
    position: absolute;
    width: 21.125em;
    display: block;
    top: 0;
    left: 2.5em;
  }
  &__top{
    max-width: 35em;
    text-align: center;
    margin: 0 auto;
  }
}

.consult-form{
  margin-top: 4em;
  display: grid;
  grid-template-columns: repeat(4,calc(25% - 21.25em / 4)) 16.25em;
  gap: 1em 1.25em;
  align-items: flex-end;
  &__btn{
    height: 3.0625em;
  }
  &__hint{
    grid-column: 1/4;
  }
  &__check{
    grid-column: 4/-1;
    order: 2;
  }
}

.invent{
  padding: 3.75em 0 3.125em;
  .info-block{
    gap: 1.5em;
    .text17{
      max-width: 28em;
    }
  }
  &__items{
    margin-top: 5em;
    padding: 0 2.5em;
    display: flex;
    justify-content: space-between;
  }
}

.invent-slider{
  margin-top: 5em;
  position: relative;
  .swiper{
    &-container{
      overflow: hidden;
      border-radius: 1.875em;
    }
    &-pagination{
      position: absolute;
      bottom: 3em;
      left: 0;
      width: 100%;
      justify-content: center;
    }
  }
  &__img{
    @include imgCover(56.1%);
    img{
      border-radius: 1.875em;
    }
  }
}

.comfort{
  padding: 3.75em 0;
  &__grid{
    display: grid;
    grid-template-columns: 24.4375em calc(100% - 26.9375em);
    gap: 2.5em;
  }
  .info-block{
    gap: 1.375em;
    text-align: left;
    .text17{
      margin: 0;
    }
  }
  &__top{
    display: flex;
  }
}

.partners{
  padding: 3.75em 0;
  &__grid{
    display: grid;
    grid-template-columns: repeat(7,calc(100% / 7 - 11.25em / 6));
    gap: 1.875em;
  }
}

.partner-item{
  img{
    width: 100%;
    display: block;
  }
}

.info{
  padding: 3.75em 0;
  &__inner{
    border-radius: 1.875em;
    background-color: $black2;
  }
}

.automation{
  padding: 4em 2.3em 5em;
  border-radius: 1.875em 1.875em 0 0;
  background-image: url(../img/automation.webp);
  background-size: cover;
  background-position: center;
  position: relative;
  color: #fff;
  &:before{
    content: '';
    position: absolute;
    z-index: 2;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0.00) 26.75%, rgba(0, 0, 0, 0.70) 100%);
    border-radius: inherit;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
  }
  &__inner{
    position: relative;
    z-index: 3;
  }
  &__top{
    max-width: 31em;
    @include grid100gap(.75em);
  }
  &__items{
    margin-top: 4em;
    border-top: 2px solid #FFF;
    display: grid;
    grid-template-columns: 16em auto 14.25em 19.3125em;
    gap: 2.75em;
    @media screen and (max-width: 1440px){
      gap: 2em;
    }
  }
}

.projects{
  padding: 2.5em 0;
  overflow: hidden;
  .info-block{
    max-width: 32em;
  }
  &__nav{
    display: flex;
    justify-content: center;
  }
}

.projects-slider{
  margin-top: 2.5em;
  .swiper{
    &-slide{
      width: 40.625em;
      padding-right: 1.25em;
    }
    &-pagination{
      margin-top: 4em;
    }
  }
}

.sensor{
  padding: 2.44em 2.1875em 2.57em;
  color: #fff;
  &__grid{
    margin-top: 2.5em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 5em / 3));
    gap: 2.5em;
  }
}

.about{
  padding: 3.75em 0 2.5em;
  .info-block{
    gap: 1.5em;
    .text17{
      max-width: 27em;
    }
  }
  &__items{
    margin-top: 5em;
    display: grid;
    grid-template-columns: repeat(4,16.75em);
    justify-content: space-between;
  }
  &__text{
    margin-top: 1.25em;
    max-width: 14.2em;
  }
}

.switches{
  padding: 2.5em 2.1875em 2.5em;
  color: #fff;
  .info-block{
    max-width: 47em;
    gap: 1.38em;
    .bold-text{
      color: #fff;
    }
    .text17{
      max-width: 46em;
    }
  }
  &__badges{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: -.31em auto 0;
    max-width: 35em;
    .badge{
      margin: .31em .16em 0;
      background: rgba(230, 230, 230, 0.20);
      color: #fff;
    }
  }
  &__partners{
    margin-top: 2.5em;
    display: flex;
    justify-content: center;
    align-items: center;
    .partner-item{
      margin: 0 2.65em;
    }
  }
}

.switches-info{
  margin-top: 2.5em;
  position: relative;
  &__video{
    video{
      width: 100%;
      border-radius: 2.5em;
    }
  }
  &__content{
    position: absolute;
    right: 2em;
    bottom: 2.81em;
    width: 27.1875em;
    @include grid100gap(2em);
  }
  &__nav{
    display: flex;
  }
}

.apps{
  padding: 2.5em 2.1875em 7.5em;
  color: #fff;
  .text48{
    text-align: center;
  }
  &__grid{
    margin-top: 4em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.25em));
    gap: 2.5em;
  }
}



@media screen and (max-width: 992px){
  .main{
    &:before{
      height: 53.5em;
      background-size: auto 100%;
    }

    .info-block{
      max-width: 24em;
    }
  }

  @media screen and (min-width: 577px){
    .home-item{
      font-size: .625em;
      .home-card{
        width: 18em;
      }
    }
  }

  .can{
    padding: 2em 0;
    .info-block{
      gap: .75em;
    }
    &__items{
      @include grid100gap(1.25em);
      width: 100%;
    }
  }

  .solution{
    padding: 2.5em 0;
    .info-block{
      gap: .75em;
      max-width: 18em;
    }
    &__grid{
      margin-top: 2.5em;
      grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
      gap: 1.25em;
    }
  }

  .map{
    margin-top: 4em;
  }

  .opportunity{
    padding: 2.25em 0 4em;
    &__top{
      max-width: 23em;
    }
    &__grid{
      margin-top: 2.5em;
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 1.25em;
    }
  }

  .guarantee{
    &__inner{
      border-radius: 1.25em;
      padding: 2.5em 1em 28.625em;
      background-image: url(../img/guarantee-bg2.webp);
    }
    &__content{
      gap: 1.5em;
      text-align: center;
      margin: 0 auto;
    }
    &__img{
      right: 50%;
      bottom: 3.125em;
      top: unset;
      transform: translateX(53%);
      width: 34em;
    }
    &__top,
    &__nav{
      justify-content: center;
    }
    &__btn{
      &:not(:hover){
        background: $black;
        color: #fff;
      }
    }
  }

  .consult{
    padding: 3.625em 0 2.25em;
    &__inner{
      padding: 2em 1.25em;
      border-radius: 1.25em;
      background: linear-gradient(127deg, rgba(255, 153, 0, 0.00) 0%, rgba(255, 153, 0, 0.30) 100%), linear-gradient(309deg, rgba(0, 240, 255, 0.00) 0%, rgba(0, 178, 255, 0.30) 100%), #E8E8E8;
    }
    &__img{
      display: none;
    }
  }

  .consult-form{
    margin-top: 1.5em;
    grid-template-columns: 100%;
    gap: 1.25em;
    &__hint{
      grid-column: 1/-1;
      color: $gray;
    }
    &__check{
      order: unset;
      grid-column: 1/-1;
    }
  }

  .invent{
    padding: 3.75em 0 3.125em;
    .info-block{
      gap: .5em;
      padding: 0 .625em;
    }
    &__items{
      padding: 0;
      margin-top: 2.5em;
    }
  }

  .invent-slider{
    margin-top: 2.5em;
    margin-left: -.625em;
    width: calc(100% + 1.25em);
    &__img img,
    .swiper-container{
      border-radius: 1.25em;
    }
  }

  .comfort{
    padding: 2.5em 0;
    .container{
      padding: 0;
    }
    .info-block{
      text-align: center;
      gap: .75em;
      padding: 0 .625em;
      .text17{
        margin: 0 auto;
      }
    }
    &__top{
      justify-content: center;
      margin-bottom: .5em;
    }
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .partners{
    padding: 2.5em 0;
    &__grid{
      grid-template-columns: repeat(4,calc(25% - 7.5em / 4));
      gap: 2.5em;
    }
  }

  .info{
    padding: 2.5em 0;
    .container{
      padding: 0;
    }
    &__inner{
      border-radius: 1.25em;
    }
  }

  .automation{
    padding: 2.5em .625em 5em;
    border-radius: 1.25em 1.25em 0 0;
    &__top{
      max-width: 19em;
      gap: .5em;
    }
    &__items{
      margin-top: 3em;
      border-top: none;
      border-left: 2px solid #FFF;
      grid-template-columns: 100%;
      gap: 1.37em;
    }
  }

  .projects{
    .info-block{
      max-width: 20em;
    }
    &__nav{
      margin-top: 2.5em;
    }
  }

  .projects-slider{
    margin-top: 2.5em;
    .swiper{
      &-slide{
        width: 21.25em;
      }
      &-pagination{
        margin-top: 1.25em;
      }
    }
  }

  .sensor{
    padding: 5em .625em 2.5em;
    color: #fff;
    &__grid{
      margin-top: 1.5em;
      grid-template-columns: repeat(2,calc(50% - .75em));
      gap: 1.5em;
    }
  }

  .about{
    padding: 2.5em 0;
    .info-block{
      gap: 1.25em;
    }
    &__items{
      grid-template-columns: repeat(2,calc(50% - 1.25em));
      gap: 2.5em;
      margin-top: 2.5em;
      .text48{
        font-size: 3em;
      }
    }
  }

  .switches {
    padding: 2.5em .625em 2.5em;
    .info-block{
      gap: .5em;
    }
    &__badges{
      margin-top: .5em;
    }
    &__partners{
      display: grid;
      grid-template-columns: repeat(4,calc(25% - 7.5em / 4));
      gap: 2.5em;
      .partner-item{
        margin: 0;
      }
    }
  }

  .switches-info{
    &__video{
      video{
        border-radius: 1.25em;
      }
    }
    &__content{
      right: 1em;
      bottom: 1em;
      width:  20em;
      gap: 1em;
    }
  }

  .apps{
    padding: 2.5em .625em 5em;
    &__grid{
      margin-top: 2em;
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 1.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    padding: 5em 0 2em;
    overflow: hidden;
    &:before{
      background-image: url(../img/main-bg2.svg);
      background-size: 100% auto;
    }
    &__top{
      position: static;
      transform: unset;
    }
  }

  .home{
    &__mob{
      position: relative;
      margin-top: 4em;
      overflow: hidden;
      border-radius: 1.875em;
    }
    &__items{
      display: none;
    }
    &__img{
      img{
        border-radius: 1.875em;
      }
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }

  .mob-rect{
    display: block;
    position: absolute;
    width: 9.6875em;
    height: 9.6875em;
    box-shadow: 0 0 0 32.5em rgba(0, 0, 0, .2);
    border-radius: 1.875em;
    transform: translate(-10%,-50%);
    transition: all .3s;
  }

  .home-card{
    position: static;
    opacity: 1;
    pointer-events: unset;
    transform: unset;
    width: 100%;
    &__content{
      border-radius: 1.875em;
    }
  }

  .home-slider{
    display: block;
    margin-top: 1.25em;
    width: 20.625em;
    .swiper{
      &-slide{
        padding-right: 1.25em;
      }
    }
  }

  .solution,
  .opportunity{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .invent{
    &__items{
      @include grid100gap(2.5em);
    }
  }

  .partners{
    &__grid{
      padding: 0 1.25em;
      grid-template-columns: repeat(2,calc(50% - 1.25em));
    }
  }

  .sensor{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .about{
    &__items{
      grid-template-columns: 100%;
    }
    &__item{
      display: grid;
      grid-template-columns: 11.6em calc(100% - 11.6em);
      align-items: center;
    }
    &__text{
      margin: 0;
    }
  }

  .switches{
    &__badges{
      max-width: 20em;
    }
    &__partners{
      padding: 0 1.25em;
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 1.25em;
      .partner-item{
        @include center;
        img{
          width: unset;
          max-width: 100%;
        }
      }
    }
  }

  .switches-info{
    &__content{
      text-align: center;
      margin-top: 2.5em;
      position: static;
      width:  100%;
      gap: 2em;
    }
    &__nav{
      justify-content: center;
    }
  }

  .apps{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}
