@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 1.875em;
    font-weight: 600;
    height: 2.25em;
    border-radius: .75em;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 0.8125em;
        line-height: 130%;
    }
}

.m-btn-white,
.m-btn-primary{
    color: $black;
    &:hover{
        color: $black;
    }
    &:active{
        background-color: $secondary;
        color: $black;
    }
    &:disabled{
        background-color: $gray;
        color: $black;
    }
}

.m-btn-primary{
    background-color: #FFA34C;
    &:hover{
        background-color: $primary;
    }
}

.m-btn-white{
    background-color: #fff;
    &:hover{
        background-color: $gray3;
    }
}

.m-btn-black{
    background-color: $black;
    color: #fff;
    &:hover{
        background-color: $black2;
        color: #fff;
    }
    &:active{
        background-color: $secondary;
        color: #fff;
    }
    &:disabled{
        background-color: $gray;
        color: #fff;
    }
}

.m-link{
    display: flex;
    align-items: center;
    color: $black;
    span{
        font-size: 1.0625em;
        line-height: 123%;
    }
    img{
        width: 1.5em;
        flex-shrink: 0;
        display: block;
        margin-left: .5em;
    }
    &:hover{
        color: $secondary;
    }
    &:active{
        color: #00A2E1;
    }
    &:disabled{
        color: $gray;
    }
}


@media screen and (max-width: 576px){
    .m-link{
        span{
            font-size: .9375em;
        }
    }
}
