.text68{
  font-size: 4.25em;
  line-height: 97%;
}

.text48{
  font-size: 3em;
  line-height: 97%;
}

.text38{
  font-size: 2.375em;
  line-height: 94.7%;
}

.text28{
  font-size: 1.75em;
  line-height: 120%;
}

.text24{
  font-size: 1.5em;
  line-height: 140%;
}

.text20{
  font-size: 1.25em;
  line-height: 125%;
}

._list li,
.text17{
  font-size: 1.0625em;
  line-height: 123%;
}

.text14{
  font-size: .875em;
  line-height: 143%;
}

.text13{
  font-size: .8125em;
  line-height: 123%;
}

.bold-text{
  font-weight: 700;
}

.semi-bold-text{
  font-weight: 600;
}

.gray-text{
  color: $gray;
}

._list{
  padding-left: .625em;
  li{
    display: flex;
    &:before{
      content: '';
      display: block;
      flex-shrink: 0;
      width: .1875em;
      height: .1875em;
      background-color: $black;
      border-radius: 50%;
      margin-right: .625em;
      transform: translateY(.5em);
    }
  }
}



@media screen and (max-width: 992px){
  .text68{
    font-size: 3em;
    line-height: 123%;
  }

  .text48{
    font-size: 1.5em;
    line-height: 121%;
  }

  ._list li{
    font-size: .9375em;
  }

  .text30-mob{
    font-size: 1.875em;
    line-height: 123%;
  }

  .text24-mob{
    font-size: 1.5em;
    line-height: 121%;
  }

  .text20-mob{
    font-size: 1.25em;
  }

  .text15-mob{
    font-size: .9375em;
    line-height: 140%;
  }
}
