@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
.info-block{
  margin: 0 auto;
  max-width: 34em;
  text-align: center;
  @include grid100gap(.75em);
  .bold-text{
    color: $black2;
  }
  .text17{
    max-width: 20em;
    width: 100%;
    margin: 0 auto;
  }
}

.calc-btn{
  padding: 0 1.38em 0 5.3125em;
  background-color: $black;
  height: 4.9375em;
  display: flex;
  align-items: center;
  border-radius: 2.8125em;
  font-weight: 600;
  color: #fff;
  position: relative;
  transition: all .3s;
  width: 13.19em;
  overflow: hidden;
  &__wrap{
    display: flex;
  }
  &__icon{
    width: 3.6875em;
    height: 3.6875em;
    @include center;
    border-radius: 50%;
    position: absolute;
    top: .625em;
    left: .625em;
    background: linear-gradient(135deg, #FC891C 14.04%, #F6FCFF 84.45%);
    transition: all .6s;
    z-index: 2;
    text-align: left;
  }
  &__text{
    position: absolute;
    right: 1.2em;
    top: 50%;
    transform: translateY(-50%);
    transition: right .6s, filter .3s;
  }
  &._hover-state{
    background: linear-gradient(0deg, rgba(52, 199, 89, 0.50) 0%, rgba(52, 199, 89, 0.50) 100%), #444;
    padding: 0 3.4em;
    .calc-btn__icon{
      left: 100%;
      transform: translateX(-100%);
      margin-left: -.625em;
    }
    .calc-btn__text{
      right: 100%;
      transform: translate(100%,-50%);
      margin-right: -1.25em;
      filter: blur(2px);
    }
  }
  &._hover-state2{
    background: #34C759;
    .calc-btn__icon{
      //left: 8.87em;
      //transform: translateX(0%);
    }
    .calc-btn__text{
      filter: blur(0);
    }
  }
}

.swiper-pagination{
  position: static;
  margin-top: 1.5em;
  display: flex;
  align-items: center;
  justify-content: unset;
  &-bullet{
    opacity: 1;
    background-color: rgba(25, 26, 31, 0.25);
    width: 0.375em;
    height: 0.375em;
    margin: 0 1.25em 0 0 !important;
    border-radius: .25em;
    &-active{
      width: 2em;
      background: #2B2B2B;
    }
  }
  &._white{
    .swiper-pagination-bullet{
      background-color: rgba(#fff,.3);
      &-active{
        background-color: #fff;
      }
    }
  }
}

.badge{
  padding: 0 .875em;
  height: 2em;
  display: flex;
  align-items: center;
  border-radius: 2.5em;
  background-color: $gray2;
  color: #5C5C5C;
  font-weight: 600;
}

