@import "../vars.scss";
.breadcrumbs {
    &__item {
        font-size: 1.0625em;
        line-height: 124%;
        position: relative;
        font-weight: 400;
        display: inline;
        color: $gray;
        &:last-child {
            color: $black;
            font-weight: 600;
        }
        &:not(:first-child) {
            &:before {
                content: '/';
                margin: 0 .375em;
                flex-shrink: 0;
                color: $gray;
            }
        }
    }
}


@media screen and (max-width: 992px){
    .breadcrumbs{
        display: none;
    }
}
