.can-card{
  margin: 1.5em 1.5em 0 0;
  padding: 3.25em 3.75em;
  background-color: $gray2;
  border-radius: 1.875em;
  min-height: 21.4375em;
  width: calc(100% - 1.5em);
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  &:hover{
    color: $black;
  }
  &._small{
    width: calc(45% - 1.5em);
  }
  &._medium{
    width: calc(55% - 1.5em);
  }
  &._big{
    .can-card__img{
      max-height: calc(100% - 2.7em);
    }
  }
  &__content{
    max-width: calc(100% - 5em);
    position: relative;
    z-index: 2;
  }
  &__text{
    margin-top: 1em;
  }
  &__paralax,
  &__line{
    position: absolute;
    right: 0;
    top: 0;
    height: 100%;
    width: auto;
  }
  &__paralax{
    width: 31em;
    z-index: 2;
  }
  &__img{
    position: absolute;
    right: 0;
    bottom: 0;
    max-height: calc(100% - 4.7em);
    max-width: 70%;
    display: block;
  }
  &__chat{
    position: absolute;
    right: 20.38em;
    top: 0;
    padding-top: 7.75em;
    height: 100%;
    width: 25em;
    &:after,
    &:before{
      content: '';
      position: absolute;
      left: 0;
      width: 100%;
      height: 3.125em;
      z-index: 2;
      background: linear-gradient(180deg, rgba(217, 217, 217, 0.00) 0%, #E8E8E8 100%);
    }
    &:before{
      left: 10%;
      top: 0;
      transform: rotate(-180deg);
    }
    &:after{
      bottom: 0;

    }
  }
  &__paralax2{
    display: none;
  }
}

.paralax-item{
  position: absolute;
}

.paralax-item1{
  left: 11.2%;
  width: 4.375em;
  top: 29%;
}

.paralax-item2{
  top: 8.7%;
  left: 36%;
  width: 4.5em;
}

.paralax-item3{
  top: 19.8%;
  left: 23%;
  width: 14.8125em;
}

.paralax-item4{
  top: 4.7%;
  right: 9.6%;
  width: 6.5em;
}

.paralax-item5{
  right: 8%;
  bottom: 16.3%;
}

.messages{
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.message{
  min-height: 3.125em;
  display: flex;
  align-items: center;
  padding: .625em 1.875em;
  min-width: 11.8125em;
  max-width: 13.4em;
  border-radius: 1.875em 1.875em 0 1.875em;
  background: linear-gradient(135deg, rgba(188, 102, 255, 0.30) 0%, rgba(227, 114, 255, 0.30) 30.33%, rgba(201, 114, 255, 0.30) 60.42%, rgba(255, 114, 114, 0.30) 94.27%);
  font-weight: 600;
  flex-shrink: 0;
  &:not(:last-child){
    margin-bottom: .3125em;
  }
  @for $i from 1 through 10 {
    &:nth-child(#{$i}) {
      margin-right: calc(1.25em * #{$i});
    }
  }
}

.solution-card{
  transition: all .2s;
  position: relative;
  color: #fff;
  &__img{
    border-radius: .5em;
    overflow: hidden;
    @include imgCover(169.1%);
    img{
      border-radius: inherit;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 3;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.30) 100%;
      border-radius: inherit;
    }
  }
  &__top{
    position: absolute;
    top: 0;
    left: 0;
    padding: 1.5em;
    z-index: 3;
    width: 100%;
  }
  &__arrow{
    position: absolute;
    right: 1.5em;
    bottom: 1.5em;
    z-index: 3;
    width: 4.125em;
  }
  @media screen and (min-width: 993px){
    &:nth-child(3n-2){
      &:hover{
        transform: translateX(-1em) scale(1.14);
      }
    }
    &:nth-child(3n-1){
      &:hover{
        transform: scale(1.14);
      }
    }
    &:nth-child(3n){
      &:hover{
        transform: translateX(1em) scale(1.14);
      }
    }
  }
}

.opportunity-card{
  padding: 2em;
  border-radius: 1.875em;
  backdrop-filter: blur(11px);
  background-color: $gray2;
  display: flex;
  flex-direction: column;
  align-items: center;
  &__info{
    margin: 0 auto 1.5em;
    text-align: center;
    max-width: 25em;
    width: 100%;
    @include grid100gap(.75em);
  }
  &__icon{
    margin-top: auto;
    height: 7.5em;
    img{
      height: 100%;
      width: auto;
      display: block;
    }
  }
}

.invent-item{
  text-align: center;
  @include grid100gap(.75em);
  max-width: 19.375em;
}

.comfort-item{
  display: grid;
  grid-template-columns: 50% 50%;
  position: relative;
  border-top: 1px solid rgba($black2,.2);
  min-height: 14.75em;
  &:nth-child(even){
    grid-template-columns: calc(50% - 4.19em) calc(50% + 4.19em);
  }
  &:last-child{
    border-bottom: 1px solid rgba($black2,.2);
  }
  &__content{
    padding: 2em 1.75em 1.75em;
    border-right: 1px solid rgba($black2,.2);
  }
  &__info{
    max-width: 19.5em;
    @include grid100gap(.75em);
  }
  &__nav{
    display: flex;
    margin-top: .75em;
    .m-link{
      color: $secondary;
      &:hover{
        color: #00A2E1;
      }
    }
  }
  &__media{
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
  }
  &._small{
    .comfort-item__media{
      padding-right: 5.3em;
    }
    .comfort-item__img{
      width: 8.5625em;
    }
  }
  &._medium{
    .comfort-item__media{
      padding-right: 6.625em;
    }
    .comfort-item__img{
      width: 15.125em;
    }
  }
  &._full-height{
    .comfort-item__media{
      padding-right: 7em;
    }
    .comfort-item__img{
      height: 100%;
      width: auto;
    }
  }
  &._big{
    .comfort-item__media{
      padding-right: 2.12em;
      position: relative;
    }
    .comfort-item__img{
      width: 26.5625em;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: 2em;
    }
  }
}

.automation-item{
  padding-top: 3em;
  position: relative;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 2px;
    height: 1.4375em;
    background-color: #fff;
  }
}

.automation-card{
  margin-top: .75em;
  padding: 1.25em;
  border-radius: 1em;
  background: rgba(255, 255, 255, 0.30);
  backdrop-filter: blur(11px);
  &__top{
    display: flex;
    align-items: center;
  }
  &__equal{
    margin: 0 .5em;
    flex-shrink: 0;
    @include center;
  }
  &__item{
    min-width: 3.94em;
    padding: .25em .5em;
    border-radius: 0.75em;
    background: rgba(255, 255, 255, 0.10);
  }
  &__desc{
    margin-top: .5em;
  }
}

.project-card{
  position: relative;
  display: block;
  color: #fff;
  &:hover{
    color: #fff;
  }
  &__img{
    border-radius: .5em;
    box-shadow: 0 4px 20px -2px rgba(29, 29, 35, 0.24), 0px 0px 1px 0px rgba(0, 0, 0, 0.30);
    @include imgCover(71.42%);
    img{
      border-radius: inherit;
    }
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(180deg, rgba(48, 49, 55, 0.00) 0%, rgba(0, 0, 0, 0.50) 54.50%, rgba(0, 0, 0, 0.70) 100%);
      border-radius: inherit;
      z-index: 2;
    }
  }
  &__content{
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    z-index: 3;
  }
  &__top{
    padding: 1em 3em;
    max-width: 30em;
    .text28{
      @include truncate(2);
    }
  }
  &__bottom{
    display: grid;
    grid-template-columns: calc(100% - 9.25em) 9.25em;
    border-top: 1px solid rgba(255, 255, 255, 0.30);
  }
  &__desc{
    padding: 1em 2em 1em 3em;
    .text20{
      @include truncate(3);
    }
  }
  &__btn{
    height: 8.125em;
    width: 100%;
    @include center;
    background: rgba(255, 255, 255, 0.15);
    box-shadow: 0 4px 20px -2px rgba(50, 50, 71, 0.08), 0px 0px 1px 0px rgba(12, 26, 75, 0.10);
    border-left: 1px solid rgba(255, 255, 255, 0.30);
    img{
      width: 1.5em;
    }
  }
}

.sensor-card{
  padding: 11.625em 2.625em 1.5em;
  border-radius: 1.875em;
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(132px);
  position: relative;
  &__img{
    position: absolute;
    top: 0;
    right: 0;
    max-width: 100%;
    max-height: 17.375em;
  }
  &__info{
    @include grid100gap(.75em);
    .text28{
      max-width: 90%;
    }
  }
}

.app-card{
  min-height: 27.5em;
  position: relative;
  border-radius: 2.5em;
  overflow: hidden;
  padding: 2.5em;
  &:hover{
    .app-card{
      &__bg{
        transform: translate(-50%,-50%) scale(1);
      }
      &__img img{
        transform: scale(1.1);
      }
    }
  }
  &:before{
    content: '';
    position: absolute;
    border-radius: inherit;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0.80) 0%, rgba(0, 0, 0, 0.00) 100%);
    z-index: 2;
  }
  &__bg{
    transition: all .2s;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%) scale(1.1);
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 2.5em;
    }
  }
  &__content{
    position: relative;
    z-index: 3;
    max-width: 18.75em;
    @include grid100gap(.75em);
  }
  &__img{
    position: absolute;
    z-index: 3;
    right: 0;
    bottom: 0;
    width: 21em;
    height: 16em;
    max-width: 100%;
    max-height: 100%;
    display: flex;
    justify-content: center;
    img{
      max-width: 100%;
      max-height: 100%;
      transition: all .2s;
    }
  }
}


@media screen and (max-width: 992px){
  .can-card{
    width: 100% !important;
    margin: 0;
    padding: 1.25em;
    min-height: 16.25em;
    &._big{
      .can-card__img{
        max-height: calc(100% - 7.25em);
      }
    }
    &:after{
      content: '';
      display: block;
      height: 2em;
      width: 100%;
      margin-top: auto;
      background-image: url(../img/arrow2.svg);
      background-size: 2.0625em auto;
      background-repeat: no-repeat;
      background-position: .625em center;
    }
    &__chat{
      display: none;
    }
    &__paralax,
    &__line{
      height: 74%;
      width: 20.5em;
    }
    &__content{
      margin-bottom: 1em;
    }
    &__text{
      margin-top: .5em;
      font-weight: 400;
    }
  }

  .paralax-item{
    font-size: .5em;
  }

  .paralax-item3{
    left: 30%;
    top: 25%;
  }

  .solution-card{
    &__top{
      padding: 1.25em;
    }
    .text38{
      font-size: 1.75em;
    }
    &__arrow{
      right: 1.25em;
      bottom: 1.25em;
    }
  }

  .opportunity-card{
    padding: 2em;
    border-radius: 1.25em;
    &__info{
      gap: .5em;
      margin-bottom: 2em;
    }
    &__desc{
      font-weight: 400;
      color: #535353;
    }
  }

  .invent-item{
    padding: 0 1.25em;
  }

  .automation-item{
    padding: 0 3.125em 0;
    &:before{
      height: 2px;
      width: 2.5em;
    }
  }

  .project-card{
    &__img{
      padding-bottom: 140.625%;
      border-radius: .375em;
    }
    &__top{
      padding: 1.25em .75em;
    }
    &__bottom{
      grid-template-columns: 100%;
      border: none;
    }
    &__desc{
      padding: 0 .75em 3.8em;
      font-weight: 400;
    }
    &__btn{
      height: 6.25em;
      border-left: unset;
      border-top: 1px solid rgba(255,255,255,.3);
      justify-content: flex-end;
      background-image: url(../img/arrow5.svg);
      background-repeat: no-repeat;
      background-position: right .75em center;
      background-size: 2.0625em auto;
      img{
        display: none;
      }
    }
  }

  .sensor-card{
    padding-left: 1.5em;
    padding-right: 1.5em;
  }

  .app-card{
    border-radius: 1.25em;
    padding: 1.25em;
    min-height: 25em;
    &__bg{
      img{
        border-radius: 1.25em;
      }
    }
    &__img{
      width: 14.625em;
      height: 13.12em;
    }
    &__content{
      max-width: 15em;
    }
  }
}

@media screen and (max-width: 576px){
  .can-card{
    &__paralax,
    &__line{
      display: none;
    }
    &__paralax2{
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      width: 11.9375em;
    }
  }

  .solution-card{
    &__img{
      padding-bottom: 60%;
    }
    .text38{
      line-height: 121%;
      br{
        display: none;
      }
    }
  }

  .invent-item{
    max-width: 100%;
    gap: .5em;
  }

  .comfort-item{
    grid-template-columns: calc(100% - 10.625em) 10.625em;
    min-height: 14.5em;
    &:nth-child(even){
      grid-template-columns: 50% 50%;
    }
    &__content{
      padding: 1.25em 0 1.25em .625em;
    }
    &__info{
      .text17{
        font-weight: 400;
      }
    }
    .m-link{
      font-weight: 400;
      color: $black;
      img{
        display: none;
      }
    }
    &._small{
      .comfort-item__media{
        padding-right: .625em;
      }
      .comfort-item__img{
        width: 5em;
      }
    }
    &._medium{
      .comfort-item__media{
        padding-right: .625em;
        align-items: center;
      }
      .comfort-item__img{
        width: 9.3125em;
      }
    }
    &._full-height{
      .comfort-item__media{
        padding-right: 1.75em;
        align-items: center;
      }
      .comfort-item__img{
        height: 9.625em;
      }
    }
    &._big{
      .comfort-item__img{
        max-width: 100%;
        right: 0;
      }
    }
  }
}
