.footer{
  padding: 5em 0 7.625em;
  &__inner{
    display: flex;
    justify-content: flex-end;
  }
  &__col{
    @media screen and (min-width: 993px){
      &:nth-child(1){
        margin-right: auto;
      }
      &:not(:first-child){
        margin-left: 8.4375em;
      }
    }
  }
  &__logo{
    display: block;
    width: 7.0625em;
    img{
      width: 100%;
      display: block;
    }
  }
  .social{
    margin-top: 2.5em;
  }
  &__items{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: .85em;
  }
  &__item{
    display: flex;
    align-items: flex-start;
    color: rgba($black,.7);
    @include lastM(0 0 .5em);
    img{
      width: 1.5em;
      margin-top: -.1em;
      margin-right: .75em;
      flex-shrink: 0;
    }
    &._address{
      flex-wrap: wrap;
    }
  }
  &__address{
    @include grid100gap(.78em);
  }
  &__street{
    width: 100%;
    margin-top: .78em;
    padding-left: 2.25em;
  }
}

.social{
  display: flex;
  &__item{
    width: 2.5em;
    flex-shrink: 0;
    @include lastM(0 1.25em 0 0);
    &:hover{
      img{
        transform: translateY(-30%);
      }
    }
    img{
      width: 100%;
      display: block;
      transition: all .3s;
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    &__inner{
      justify-content: space-between;
    }
    &__col{
      @media screen and (min-width: 577px){
        @include lastM(0 1.5em 0 0);
      }
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    padding: 5em 0;
    text-align: center;
    &__inner{
      flex-direction: column;
      align-items: center;
    }
    &__col{
      @include lastM(0 0 5em);
    }
    &__logo{
      margin: 0 auto;
    }
    .text24{
      font-weight: 700;
    }
    &__items{
      margin-top: 1.15em;
      align-items: center;
    }
    &__item{
      justify-content: center;
    }
    &__street{
      padding-left: 0;
    }
  }
}
